import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import {
    colors,
    FormControl,
} from "@material-ui/core";
import styled from "styled-components";
import {SelectWidget} from "../../../widget/slect_tokens/select";
import {InputWidget} from "../../../widget/base/input";
import {useForm} from "react-hook-form";
import {ButtonWidget} from "../../../widget/base/button";
import {ApproveButton} from "../../../widget/base/approve_button";
import {useWeb3} from "../../../provider/web3_provider";
import {TokenInterFace} from "../../../web3/token.config";
import {getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";

import {useConvert} from "../../../server/sunder";
import {useSendModal} from "../../../widget/modal/send_modal";
import {WidthStyle} from "../../../widget/style/width_style";
import {TitleTextWidget} from "../../../widget/base/title";
import {CreatePairWidget} from "./create_pair";

export const RightWidget: React.FC = () => {
    const {register, handleSubmit, setValue, errors} = useForm();
    const {tokenMap: officialMap, customProjectMap, contrcts} = useWeb3();

    const tokenMap = useMemo(() => ({...officialMap, ...customProjectMap}), [customProjectMap]);
    let spenderAddress = contrcts.sunderAddress;


    const [key, setKey] = useState(Object.keys(tokenMap)[0]);


    const [tokenItem, setTokenItem] = useState<TokenInterFace>(tokenMap[key])

    const MaxNumber = useMemo(() => {
        return getBalanceNumber(tokenItem.balance || 0);
    }, [tokenItem, tokenItem.balance])

    const [facethConvert] = useConvert();
    const sendFn = useSendModal(facethConvert);

    const [amount, setAmount] = useState('0');

    const checkTokenItem = useCallback((key) => {
        setKey(key)
        setTokenItem(tokenMap[key])
    }, [setKey])

    const handleLogin = useCallback(async (data: any) => {
        let _number = getMaxNumber(amount, tokenItem.balance);
        await sendFn(tokenItem.address, _number);
    }, [tokenItem, tokenItem, amount]);


    return (
        <RightStyle>
            <div className="wap">
                <TitleTextWidget text={"Mint"}
                                 title={'Step by step process:'}
                                 arrText={[
                                     `When you mint tokens from any native Governance token, the protocol will sunder your Native tokens (e.g. COMP) into two Isolated tokens: dTokens and eTokens. dTokens provides with rights over governance over the Native Governance token , while eTokens function as the inherent earnings right of your Native token.`,
                                     `Select the Vault you would like to choose so sunder any available Native Governance token. At the moment, you can select Uniswap or Compound, with further governance tokens to be integrated along the way.`,
                                     `Enter the amount of native tokens you would like to Sunder and the interface will automatically display the amount of dTokens and eTokens minted with the transaction. For every native governance token deployed, 1 dToken and 1 eToken will be minted.`,
                                     `Click "Approve" to allow the spending limit, then "Confirm" to begin Sundering.`
                                 ]}
                />
                <div className="inner">
                    <p className={'stake'}>Choose a Vault to mint dTokens and eTokens from your native token</p>
                    <p className={'charged'}>0.1% fee charged on redemption (subject to change via governance)</p>
                    <div className={'input'}>
                        <FormControl id="demo-simple-select-label" fullWidth={true}>
                            <SelectWidget
                                listToken={tokenMap}
                                value={key}
                                setValue={checkTokenItem}
                            />
                        </FormControl>
                    </div>


                   {/* <div style={{marginTop: "20px"}}>
                        <CreatePairWidget/>
                    </div>*/}

                    <div className={'input'}>
                        <InputWidget errors={errors}
                                     inputRef={register({
                                         required: 'Please enter a number > 0',
                                         max: {value: MaxNumber, message: `Max ${MaxNumber}`},
                                     })}
                                     type={'tel'}
                                     label={'Amount of Native Tokens to Sunder'}
                                     sunTitle={
                                         <div>
                                             <p>Collateral: <a href={tokenItem.netWork}>{tokenItem.nick}</a></p>
                                         </div>
                                     }
                                     name={'amount'}
                                     onChange={(ev) => {
                                         setAmount(ev.target.value);
                                     }}
                                     placeholder={'0.00'}
                                     setValue={(ev) => {
                                         setAmount(MaxNumber.toString());
                                         setValue(ev, MaxNumber);
                                     }
                                     }
                        >
                            <div className={'balance'}>Balance: <span>{MaxNumber}</span></div>
                        </InputWidget>

                        <div className={'bottoms'}>
                            <p>You will receive {amount || 0} {tokenItem.nick} dTokens
                                and {amount || 0} {tokenItem.nick} eTokens</p>
                        </div>
                    </div>

                    <ApproveButton contract={tokenItem.contract} contractAddress={tokenItem.address}
                                   spender={spenderAddress}>
                        <div>
                            <ButtonWidget text={'Confirm'} onClick={handleSubmit(handleLogin)}/>
                        </div>
                    </ApproveButton>
                </div>
            </div>

        </RightStyle>
    );
}


const RightStyle = styled.div`

  ${() => WidthStyle.MinInnerStyle};

  .wap {
    background-size: 100% 100%;
  }

  .inner {
    padding: 10px 20px 32px 20px;
    box-sizing: border-box;
  }

  .stake {
    font-size: 14px;
    font-weight: 400;
    color: #696190FF;
    display: flex;
    justify-content: space-between;
  }

  .charged {
    font-size: 12px;
    margin-top: 5px;
    font-weight: 400;
    color: ${({theme}) => theme.color.oranges[100]};
    margin-bottom: 20px;
  }

  .input {
    margin-top: 20px;
    box-sizing: border-box;

    .bottoms {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      line-height: 20px;
      font-weight: 400;
      margin-bottom: 20px;
      margin-top: 20px;
      color: ${({theme}) => theme.color.oranges[100]};
    }
  }

  .balance {
    color: rgb(211, 211, 211);
    width: 200px;

    span {
      color: ${({theme}) => theme.color.greens[100]};

    }
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .input {
      margin-top: 20px;
    }

    .charged {
      margin-bottom: 20px;
    }

    .balance {
      width: 220px;
    }
  }
`
