import React, {createContext, useCallback, useContext, useEffect, useState} from "react";
import {Sunder} from "../web3/sunder";
import {useWallet} from "use-wallet";
import Web3 from "web3";
import {Contrcts} from "../web3/contrcts";
import WalletConnect from "@walletconnect/client";
import {TokenInterFace} from "../web3/token.config";
import {useWait} from "../utils/wait_fn";
import BigNumber from "bignumber.js";
import {getBalanceNumber} from "../utils/formatBalance";


let current: number = 0;
export const Web3Provider: React.FC = ({children}) => {

    const [nsuer, setNsuer] = useState<any>();
    const [customProjectMap, setCustom] = useState({});

    const {ethereum, chainId, getBlockNumber, connector, account} = useWallet();

    const [data, setData] = useState({
        block: current, token: {
            usdt: new BigNumber(0),
            eth: new BigNumber(0),
            btc: new BigNumber(0),
            usd: new BigNumber(0),
        }
    });

    const callBack = useCallback(async (nsuer: Sunder) => {
        if (!account) return;
        const latestBlockNumber = await nsuer.web3.eth.getBlockNumber();

        if (current !== latestBlockNumber) {
            let token: any = await tokenList({...nsuer.tokenMap, ...nsuer.customProjectMap}, nsuer.web3, account);
            setData(
                {
                    block: latestBlockNumber,
                    token,
                }
            );
            current = latestBlockNumber;
        }
    }, [ethereum, getBlockNumber, setData]);


    useEffect(() => {
            if (!ethereum) return;
            let _nsuer = new Sunder(ethereum, chainId || 0, {connector});
            setNsuer(_nsuer);
            setCustom(_nsuer.customProjectMap);
            callBack(_nsuer);
            const interval = setInterval(async () => {
                callBack(_nsuer);
            }, 3000);

            return () => clearInterval(interval)
        }, [ethereum],
    );

    return (
        <web3Context.Provider value={{
            nsuer,
            block: data.block,
            listToken: data.token,
            setCustom,
            customProjectMap
        }}>{children}</web3Context.Provider>
    )
}


const web3Context = createContext<Web3Interface>({
    nsuer: null,
    block: current,
    listToken: {},
    customProjectMap: {},
    setCustom: () => null,
})


interface Web3Interface {
    nsuer: Sunder | any,
    block: number,
    listToken: { [key: string]: BigNumber },
    customProjectMap: { [key: string]: TokenInterFace }
    setCustom: Function
}


export const useWeb3 = () => {
    const {nsuer, block, listToken, customProjectMap,setCustom} = useContext(web3Context);
    return {
        web3: nsuer.web3,
        tokenMap: nsuer.tokenMap,
        customProjectMap,
        contrcts: nsuer.contrcts as Contrcts,
        blockNumber: block,
        walletConnect: nsuer.walletConnect as WalletConnect,
        listToken: listToken,
        setCustom,
        nsuer,
    }
}


async function tokenList(tokenMap: { [key: string]: TokenInterFace }, web3: Web3, account: string) {
    const _map: any = {};

    for (const key in tokenMap) {
        const item:TokenInterFace = tokenMap[key];
        item.balance = new BigNumber(await item.contract.methods.balanceOf(account).call());
    }


    let ethBalance: any = await web3.eth.getBalance(account);
    _map['eth'] = new BigNumber(ethBalance);
    return _map;
}




