import {useWeb3} from "../../provider/web3_provider";
import styled from "styled-components";
import {useModel} from "../../hook/use_model";
import {SelectPage} from "./select_page";
import React from "react";
import {TokenInterFace} from "../../web3/token.config";
import {WidthStyle} from "../style/width_style";
import dropDown from "../../assets/images/drop_down.png"
export const SelectTopWidget:React.FC<{setKey:Function,keys:string}> = ({setKey,keys}) => {
    const {tokenMap} = useWeb3();
    const tokenItem: TokenInterFace = tokenMap[keys];
    const [handlePresent] = useModel(<SelectPage listToken={tokenMap} setValue={setKey}/>);
    return (
        <SelectStyle>
            <p>Protocol</p>
            <span/>
            <h3 onClick={handlePresent}>{tokenItem?.nick} <img src={dropDown} alt=""/></h3>
        </SelectStyle>
    );
}


const SelectStyle = styled.div`
  border-radius: 14px;
  
  ${() => WidthStyle.MinWidth};
  border: 1px solid #9691B580;
  padding: 10px 40px;
  display: flex;
  align-items: center;
  margin: 18px auto 28px auto;
  box-sizing: border-box;
  p {
    font-size: 21px;
    font-weight: 500;
    color: #2E1F73FF;
    line-height: 31px;
  }

  span {
    display: block;
    background-color: #847DA669;
    width: 2px;
    height: 30px;
    margin: 0 60px 0 40px;
  }

  h3 {
    color: #847DA6FF;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex: 1;
    img{
      display: block;
      width: 20px;
    }
  }
  
  @media(max-width: 600px){
    p{
      font-size: 18px;
    }
    span{
      margin: 0 30px 0 20px;
    }
    h3{
      font-size: 16px;
    }
  }
`;
