import {ProjectConfig} from "../../../web3/token.config";
import {useCallback} from "react";
import {CUSTOMKEY, deleteStorage, upDateStorage} from "../../../utils/localstorage";
import {useWeb3} from "../../../provider/web3_provider";
import BigNumber from "bignumber.js";


export const useAddProtoduct = (modal: ProjectConfig) => {

    const {nsuer: sunder, setCustom, customProjectMap} = useWeb3();
    const handleAdd = useCallback(() => {
        const {address} = modal;
        let data:any = {};
        data[address] = modal;
        upDateStorage(CUSTOMKEY, data);

        data['balance'] = new BigNumber(0);
        customProjectMap[address] = sunder.customProject(modal);

        setCustom({...customProjectMap});
    }, [modal]);
    return handleAdd;
}

export const useDeleteProtoduct = () => {

    const {setCustom, customProjectMap} = useWeb3();
    const handleAdd = useCallback((addr: string) => {

        delete customProjectMap[addr];
        deleteStorage(CUSTOMKEY, addr);
        setCustom({...customProjectMap});

    }, []);
    return handleAdd;
}
