import styled from "styled-components";
import {WidthStyle} from "../style/width_style";
import {CircularProgress, InputBase} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import React, {useCallback, useContext, useEffect, useState} from "react";
import {TokenInterFace} from "../../web3/token.config";
import {getBalanceNumber} from "../../utils/formatBalance";
import {ModalContext} from "../../provider/model_provider";
import {useCheckAvailable} from "../../server/sunder";
import {HttpStatus} from "../../utils/wait_fn";
import {ResultWidget} from "../../view/mint/widget/create_pair";

export const SelectPage: React.FC<{ setValue: Function, listToken: { [key: string]: TokenInterFace }, }> = ({
                                                                                                                setValue,
                                                                                                                listToken: tokenMap
                                                                                                            }) => {
    const {onDismiss} = useContext(ModalContext);
    const [select, setSelect] = useState('');
    const {createPair, data, status, setStatus} = useCheckAvailable();

    const requestCreate = useCallback(async () => {
        await createPair(select);
    }, [createPair, select, data, status]);

    useEffect(() => {
        if (!select) {
            setStatus(HttpStatus.start)
            return;
        }
        let timeOut = setTimeout(requestCreate, 500);
        return () => clearInterval(timeOut);
    }, [select])

    return (
        <WapStyle>
            <h1>Back</h1>
            <div className="search">
                <div>
                    <InputBase fullWidth={true}
                               placeholder={"Search for ticker or contract address"}
                               onChange={(ev) => {
                                   setSelect(ev.target.value);
                               }}
                    />
                    <SearchIcon/>
                </div>
            </div>
            {
                status !== HttpStatus.start && <div className={'show_data'}>
                    {status === HttpStatus.wait && <div className={'loading'}>
                        <CircularProgress color="inherit"/>
                    </div>}
                    {status === HttpStatus.failure && <div className={'err'}>{data.err}</div>}
                    {status === HttpStatus.success && <ResultWidget model={data}/>}
                </div>

            }
            <div className="body">

                {
                    Object.keys(tokenMap).map((ev) => {
                        let _token: TokenInterFace = tokenMap[ev];
                        if (!select || (_token.nick.toLowerCase()).indexOf(select.toLowerCase()) !== -1) {
                            return <CellWidget cb={() => {
                                setValue(ev);
                                onDismiss();
                            }} cell={tokenMap[ev]}/>
                        }

                    })
                }

            </div>
        </WapStyle>
    );
}


export const CellWidget: React.FC<{ cell: TokenInterFace, cb: Function }> = ({cell, cb}) => {
    return (
        <CellStyle onClick={() => cb()}>
            <img src={cell.logo} alt=""/>


            <div className={'text'}>
                <h2>{cell.stakeName}</h2>
                <p>{cell.nick}</p>
            </div>
            <p className={'tail'}>{getBalanceNumber(cell.balance || 0)}</p>
        </CellStyle>
    );
}

