import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback} from "react";
import {CreatePairWidget} from "../view/mint/widget/create_pair";


export const useDeposit = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sVaultAddress);

    const depositAll = useCallback(async () => {
        let method = contrcts.sVaultContract.methods.depositAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const deposit = useCallback(async (amount: string) => {
        let method = contrcts.sVaultContract.methods.deposit(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3

    ]);

    return {depositAll, deposit}
}

export const useWithdraw = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sVaultAddress);

    const withdrawAll = useCallback(async () => {
        let method = contrcts.sVaultContract.methods.withdrawAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const withdraw = useCallback(async (amount: string) => {
        let method = contrcts.sVaultContract.methods.withdraw(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3

    ]);

    return {withdraw, withdrawAll}
}



